import React, {useCallback, useMemo, useState} from 'react'
import './index.scss';
import cn from 'classnames';
import SimpleBar from 'simplebar-react';
import Icon from '../../../../components/Icon'
import { useView } from '../../../../state/view/hooks'
import { useList } from '../../../../state/list/hooks'
import { ago, formatAmount } from "../../../../utils";
import ContentLoader from "react-content-loader";

type TokenListProps = {
    currentTab: string,
    data: any[],
    loading: boolean
}

type MSimpleTokenListProps = {
    data: any,
    animated: boolean,
    currentTab: string,
    opt: {
        fromSymbol: string,
        toSymbol: string,
        fromDecimal: number,
        toDecimal: number
    },
    href?: string
}

const MSimpleTokenList: React.FC<MSimpleTokenListProps> = ({ href, data, animated, currentTab, opt }) => {
    const { CurrentSwapLiquidity } = useList()
    const { type, value1: from, value0: to, timestamp, amount } = data
    const { fromSymbol, toSymbol, fromDecimal, toDecimal } = opt;
    // console.log('CurrentSwapLiquidity?._target----')
    // console.log(CurrentSwapLiquidity?._target)
    // console.log(type)
    const sign = currentTab === 'liquidity' ? (type === 0 ? 'add' : 'rm') : (() => {
        if (CurrentSwapLiquidity?._target === 0) {
            return type === 3 ? 'down' : 'up'
        } else {
            return type === 3 ? 'up' : 'down'
        }
        // return (type === 2 ? 'up' : 'down')
    })()
    return (
        <li className={cn({ [sign]: 1, 'fade-enter-done': animated })} onClick={href ? () => {
            window.open(href)
        } : undefined}>
            <div className='col-1'>
                {currentTab === 'liquidity'
                    ? <div className={cn('sign', { [sign]: 1 })} />
                    : <Icon symbol={sign} size={16} />
                }
                <div className='amount'>
                    <div className='from-pair'>
                        <span className='num'>{formatAmount(from, fromDecimal, 4)}</span>
                        <span>{fromSymbol}</span>
                    </div>
                    <div className='to-pair'>
                        <span className='num'>{formatAmount(to, toDecimal, 4)}</span>
                        <span>{toSymbol}</span>
                    </div>
                </div>
            </div>
            <div className='col-2'>
              <div className='value'>
                  {amount && `$${formatAmount(amount, 0, 2)}`}
              </div>
              <div className='emoji' />
          </div>
            <div className='col-3'>{ago(timestamp)}</div>
        </li>
    )
}

const Loading:React.FC = (props) => {
    const Res = () => (
      <ContentLoader
        speed={2}
        width={360}
        height={54}
        viewBox="0 0 360 54"
        backgroundColor="#ffffff22"
        foregroundColor="#222B36"
        {...props}
      >
          <circle cx="10" cy="28" r="8" />
          <rect x="25" y="15" rx="5" ry="5" width="100" height="7" />
          <rect x="25" y="34" rx="5" ry="5" width="100" height="7" />
          <rect x="160" y="23" rx="5" ry="5" width="90" height="10" />
          <rect x="278" y="23" rx="5" ry="5" width="80" height="10" />
      </ContentLoader>
    )
    return (
      <div className='c-content-loader'>
          <Res />
          <Res />
          <Res />
          <Res />
          <Res />
          <Res />
          <Res />
          <Res />
      </div>
    )
}

const TokenList: React.FC<TokenListProps> = ({ currentTab, data = [], loading }) => {
    const { isMobile } = useView()
    // const symbol = 'USDT'
    const { CurrentSwapLiquidity } = useList()
    // console.log('CurrentSwapLiquidity---')
    // console.log(CurrentSwapLiquidity)

    const [animated, /*setAnimated*/] = useState<boolean>(false);

    const mkHref = useCallback((hash: string) => {
        let cid = CurrentSwapLiquidity?.cid
        let href = ''
        switch (cid) {
            case 1:
                href = `https://etherscan.io/tx/${hash}`;
                break;
            case 3:
                href = `https://ropsten.etherscan.io/tx/${hash}`;
                break;
        }
        return href
    }, [CurrentSwapLiquidity])

    const TokenListElement = useMemo(() => {
        let opt = { fromSymbol: '', toSymbol: '', fromDecimal: 0, toDecimal: 0 };

        if (CurrentSwapLiquidity) {
            opt = {
                fromSymbol: CurrentSwapLiquidity.token1.symbol,
                toSymbol: CurrentSwapLiquidity.token0.symbol,
                fromDecimal: CurrentSwapLiquidity.token1.decimal,
                toDecimal: CurrentSwapLiquidity.token0.decimal
            }
        }

        return data.map((item, i) => {
            let _animated = animated && i < 1
            return <MSimpleTokenList
                key={i}
                data={item}
                animated={_animated}
                currentTab={currentTab}
                opt={opt}
                href={mkHref(item.hash)}
            />
        })
    }, [CurrentSwapLiquidity, data, animated, currentTab, mkHref])

    // console.log('data---')
    // console.log(data)

    return (
        <div className='token-list'>
            <div className='token-list-header'>
                <span>Token Amount</span>
                <span>{`Token Value, $`}</span>
                <span>Time</span>
            </div>
            {loading
              ? (<Loading />)
              : isMobile ? (
                <div className='m-list__body'>
                    {(data && data.length) ? <ul>{TokenListElement}</ul> : <div className='no-data'>No Data</div>}
                </div>
              ) : (
                <SimpleBar forceVisible='y'> {/*style={{ height: '320px' }}*/}
                    {(data && data.length) ? <ul>{TokenListElement}</ul> : <div className='no-data'>No Data</div>}
                </SimpleBar>
              )
            }
        </div>
    )
}
export default TokenList
