import React, { useState, useCallback, useMemo } from "react";
import cn from "classnames";
import _ from "lodash";
import Input, { EstimateValue } from "./Input";
import { Currency } from "@uniswap/sdk";
import { useActiveWeb3React } from "../../../hooks";
import { Field } from "../../../state/swap/actions";
import { useCurrencyBalance } from "../../../state/wallet/hooks";
import { ApprovalState } from "../../../hooks/useApproveCallback";
import SwapModal from "./SwapModal";
import Pendding from "../../../components/PendingLoader";
import Dats, { SDK_TYPE } from "../../../SDK";
import "./index.scss";

export interface InputAndOutputTokenType {
  Token: Currency | undefined;
  Logo: string | undefined;
}

interface ButtonProps {
  ButtonError: string | undefined;
  approvedState: ApprovalState;
  _className: string;
  btnText: string;
  currency: InputAndOutputTokenType | undefined;
  onSwap: () => void;
  onApproved: () => void;
}

export const Button: React.FC<ButtonProps> = ({
  ButtonError,
  approvedState,
  _className,
  btnText,
  currency,
  onApproved,
  onSwap,
}) => {
  if (ButtonError) {
    return (
      <button className={cn("trade-button dsb", { [_className]: 1 })}>
        {ButtonError}
      </button>
    );
  }

  if (
    approvedState === ApprovalState.PENDING ||
    approvedState === ApprovalState.NOT_APPROVED
  ) {
    return (
      <button
        className={cn("trade-button", { [_className]: 1 })}
        onClick={() => {
          if (approvedState === ApprovalState.NOT_APPROVED) {
            onApproved();
          }
        }}
      >
        {approvedState === ApprovalState.PENDING ? (
          <span className="approvaling">
            Approving
            <Pendding />
          </span>
        ) : (
          `Approve ${currency?.Token?.symbol}`
        )}
      </button>
    );
  }

  return (
    <button
      onClick={onSwap}
      className={cn("trade-button", { [_className]: 1 })}
    >
      {btnText}
    </button>
  );
};

const SwapForm: React.FC<{
  field: Field;
}> = ({ field }) => {
  const [showConfirmModal, setConfirm] = useState<boolean>(false);
  const { account } = useActiveWeb3React();

  const toggleConfirmModal = () => {
    setConfirm(!showConfirmModal);
  };

  const isExactBuy: boolean = field === Field.BUY;
  const [_className, btnText] = useMemo(() => {
    return isExactBuy ? ["buy", "Buy"] : ["sell", "Sell"];
  }, [isExactBuy]);
  const { useSwapInfo, useApproved } = Dats[SDK_TYPE.UNISWAP];

  const { typedValue, parsedAmount, pair, trade } = useSwapInfo(field);
  // console.log('useSwapInfo---')
  // console.log(field,)
  // console.log(typedValue, parsedAmount, pair, trade)
  const { approval, approveCallback } = useApproved(field);
  const [inputToken, outputToken] = pair;

  const balanceIn = useCurrencyBalance(account ?? undefined, inputToken?.Token);

  const ButtonError = useMemo(() => {
    if (balanceIn && parsedAmount && balanceIn.lessThan(parsedAmount)) {
      return "Insufficient " + inputToken?.Token?.symbol + " balance";
    }
    if (!parsedAmount) {
      return `Enter an Amount`;
    }
  }, [balanceIn, parsedAmount, inputToken]);

  return (
    <>
      <div className={cn("swap-input", { [_className]: 1 })}>
        <div className="from-input">
          <Input
            selectToken={false}
            field={field}
            value={typedValue}
            currency={inputToken}
            balanceIn={balanceIn}
          />
          <EstimateValue
            currency={outputToken}
            value={trade?.outputAmount?.toSignificant(6)}
          />
        </div>
        <div className="btn">
          <Button
            approvedState={approval}
            ButtonError={ButtonError}
            btnText={btnText}
            currency={inputToken}
            _className={_className}
            onSwap={() => {
              setConfirm(true);
            }}
            onApproved={approveCallback}
          />
        </div>
      </div>
      {showConfirmModal && (
        <SwapModal
          field={field}
          title={btnText}
          trade={trade ?? undefined}
          pair={pair}
          toggleConfirmModal={toggleConfirmModal}
        />
      )}
    </>
  );
};

const Swap: React.FC = () => {
  return (
    <div className="c-swap">
      <div className="c-formWrapper">
        <SwapForm field={Field.BUY} />
        <div className="devider" />
        <SwapForm field={Field.SELL} />
      </div>
    </div>
  );
};

export default Swap;
