import { useMemo } from 'react'
import { useActiveWeb3React } from "../../hooks";
import { Token, Currency, ETHER } from "@uniswap/sdk";
import { tryParseAmount } from "../../state/swap/hooks";
import { useTradeExactIn } from "../../hooks/Trades";
import { Field } from "../../state/swap/actions";
import { useSwapState } from "../../state/swap/hooks";
import { useCurrencyBalance } from "../../state/wallet/hooks";
import { useUserState } from "../../state/user/hooks";
import { useList } from "../../state/list/hooks";


export interface InputAndOutputTokenType {
    Token: Currency | undefined;
    Logo: string | undefined;
}

export const getExactType = (field: Field.BUY | Field.SELL): boolean => field === Field.BUY


export const usePair = (field: Field.BUY | Field.SELL) => {
    const { chainId } = useActiveWeb3React();
    const { CurrentSwapLiquidity, TargetToken } = useList();

    const isExactBuy = getExactType(field)

    let pair = useMemo(() => {
        if (!CurrentSwapLiquidity || !TargetToken) return null;
        const { token0, token1 } = CurrentSwapLiquidity;

        if (token0.name === TargetToken.name) {
            return [token0, token1];
        }
        return [token1, token0];
    }, [CurrentSwapLiquidity, TargetToken]);

    pair = pair ? (isExactBuy ? pair : pair?.reverse()) : [];
    if (!pair || !chainId) {
        return [undefined, undefined]
    }

    return pair.map(
        (t): InputAndOutputTokenType => {
            return {
                Logo: t.logo,
                Token:
                    t.name.toLocaleUpperCase() === "ETH" ||
                        t.name.toLocaleUpperCase() === "WETH"
                        ? ETHER
                        : new Token(chainId, t.address, t.decimal, t.symbol),
            };
        }
    )
}

